import Vue from 'vue';
import VueRouter from 'vue-router';
import Potter from '../views/Potter.vue';
import Swapi from '../views/Swapi.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'potter',
    component: Potter,
  },
  {
    path: '/swapi',
    name: 'swapi',
    component: Swapi,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
