<template>
  <div class="potter">
    <img alt="Vue logo" src="../assets/logo.png" />
    <PageTitle title="Potter API" msg="Welcome to the Potter API page" />
    <div>
      <h1>This is the characters page.</h1>
      <form v-on:submit.prevent="getCharacters">
        <label>Show me members of: </label>
        <select v-model="query">
          <option disabled value>Please select one</option>
          <option value="Hufflepuff">Hufflepuff</option>
          <option value="Gryffindor">Gryffindor</option>
          <option value="Slytherin">Slytherin</option>
          <option value="Ravenclaw">Ravenclaw</option>
        </select>
        <button type="sumbit">Go</button>
      </form>
      <b-container fluid>
        <b-row
          class="results-row"
          v-if="characters && characters.length > 0"
          align-h="around"
        >
          <b-col
            md="5"
            lg="3"
            v-for="character in characters"
            :key="character.name"
            class="m-2 result-card"
            :class="newStyle"
          >
            <h4>{{ character.name }}</h4>
            <b-img v-bind:src="character.image" fluid></b-img>
            <p v-if="character.patronus">Patronus: {{ character.patronus }}</p>
            <p>House: {{ character.house }}</p>
            <p v-if="character.wand.wood">
              Wand: {{ character.wand.wood }}
              {{ character.wand.core }}
            </p>
          </b-col>
        </b-row>
      </b-container>
    </div>
  </div>
</template>

<script>
  import PageTitle from '@/components/PageTitle.vue';
  import axios from 'axios';

  export default {
    name: 'Potter',
    components: {
      PageTitle,
    },
    data() {
      return {
        characters: [],
        errors: [],
        newStyle: [],
        query: ['Hufflepuff', 'Gryffindor', 'Slytherin', 'Ravenclaw'],
      };
    },

    methods: {
      getCharacters() {
        axios
          .get(
            'https://hp-api.herokuapp.com/api/characters/house/' + this.query
          )
          .then((result) => {
            console.log(result.data);
            this.characters = result.data;
            this.newStyle = this.query;
          })
          .catch((error) => {
            console.log(error);
          });
      },
    },
  };
</script>

