<template>
  <div class="swapi">
    <img alt="Vue logo" src="../assets/logo.png" />
    <PageTitle title="SWAPI" msg="Welcome to the Star Wars API page" />
    <div>
      <h1>This is the Star Wars page.</h1>
      <form v-on:submit.prevent="getResults">
        <label class="mx-2">Show me a list of: </label>
        <select v-model="query">
          <option value="null">Please select one</option>
          <option value="people">People</option>
          <option value="planets">Planets</option>
          <option value="species">Species</option>
          <option value="starships">Starships</option>
          <option value="vehicles">Vehicles</option>
        </select>
        <button type="sumbit">Go</button>
      </form>
      <b-container fluid>
        <ul class="results-row" v-if="results && results.length > 0">
          <li v-for="result in results" :key="result.name" class="result-card">
            <h4>{{ result.name }}</h4>
          </li>
        </ul>
      </b-container>
    </div>
  </div>
</template>

<script>
  import PageTitle from '@/components/PageTitle.vue';
  import axios from 'axios';

  export default {
    name: 'test',
    components: {
      PageTitle,
    },
    data() {
      return {
        results: [],
        errors: [],
        query: ['people', 'planets', 'species', 'starships'],
      };
    },

    methods: {
      getResults() {
        axios
          .get('https://swapi.dev/api/' + this.query)
          .then((result) => {
            console.log(result.data.results);
            this.results = result.data.results;
          })
          .catch((error) => {
            console.log(error);
          });
      },
    },
  };
</script>

